import Auth from '../../utils/Auth';

// Export Constants
export const SIGN_IN = 'SIGN_IN';
export const SETUP_PRACTICE = 'SETUP_PRACTICE';
export const SETUP_PRACTICE_FOR_LOGGEDIN_USER = 'SETUP_PRACTICE_FOR_LOGGEDIN_USER';
export const SESSION_EXPIRED = 'SESSION_EXPIRED';
export const SIGN_OUT = 'SIGN_OUT';
export const UPDATE_USER_PROFILE = 'UPDATE_USER_PROFILE';
export const SURVEY_SUBMIT = 'surveySubmit';
export const SAVE_STRIP_CONNECT_ACCOUNT_ID = "SAVE_STRIP_CONNECT_ACCOUNT_ID";
export const SAVE_SUBSCRIPTION_ID = "SAVE_SUBSCRIPTION_ID";

export const SPECIALITIES_LIST = 'SPECIALITIES_LIST';

/**
 * Actions related to the authentication module
 */

/**
 * Action called after sign in api call
 * @param data
 * @returns {{type: string, user: (*|number), accessToken: *}}
 */
export function signIn(data) {
  return {
    type: SIGN_IN,
    user: data.user,
    accessToken: data.access_token
  };
}

export function setPracticeInLoggedInUser(data){
  return {
    type: SETUP_PRACTICE,
    practice: data
  }
}
export function setPracticeForLoggedInUser(data){
  return {
    type: SETUP_PRACTICE_FOR_LOGGEDIN_USER,
    practice: data
  }
}

/**
 * User logout/signout action
 * @returns {{type: string, data: *}}
 */
export function signOut() {
  Auth.logoutAndNavigateToLogin();
  return {
    type: SIGN_OUT
  }
}

export function updateProfileImage(imageUrl) {
  return {
    type: UPDATE_USER_PROFILE,
    imageUrl
  }
}

export function sessionExpired(error = 'Session expired') {
  return {
    type: SESSION_EXPIRED,
    error
  }
}

export function getAllSpecalities(specialities){
  return {
    type: SPECIALITIES_LIST,
    specialities
  }
}


// stripeConnectAccountId
export function saveStripConnectAccountId(id){
  return {
    type: SAVE_STRIP_CONNECT_ACCOUNT_ID,
    id
  };
}

export function saveSubscriptionId(id){
  return {
    type: SAVE_SUBSCRIPTION_ID,
    id
  };
}