import React from 'react';

import {connect} from 'react-redux';
import { Grid } from '@material-ui/core';

import { getPracticeDetailsRequest } from '../../PracticeApiActions';
import MessageModal from "../../../../utils/components/MessageModal/MessageModal";
import UserTable from '../../../User/components/UserTable/UserTable';

/**
 * Component to add/update prectice information
 */
class SetupPracticeStepFour extends React.Component{

    constructor(props) {
        super(props);

        this.state = {
        }
    }

    componentDidMount() {
    
    }
    
    render(){
        return(
            <>
                <div>
                    <Grid container spacing={3}>
                        <Grid item xs={12}>
                            <UserTable onSkip={() => this.props.finishSetup()}/>
                        </Grid>
                    </Grid>
                </div>

                <MessageModal
                    ref="messageModal"
                />
            </>
        )
    }

}

const mapStateToProps = (state) => {
    return {
        loggedInUser: state.auth.loggedInUser,
        currentRole: state.auth.currentRole
    }
};

const mapDispatchToProps = (dispatch) => {
  return {
    getPracticeDetails: (practiceId) => {
      return dispatch(getPracticeDetailsRequest(practiceId)).then((res) => {
        return Promise.resolve(res);
      }, (error) => {
        return Promise.reject(error);
      });
    }
  }
};

SetupPracticeStepFour = connect(
  mapStateToProps,
  mapDispatchToProps
)(SetupPracticeStepFour);

export default SetupPracticeStepFour;